import React, { Component } from 'react'
import { Typography } from '@material-ui/core'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import SwipeableViews from 'react-swipeable-views'
import Card from './Card'

const styles = {
  root: {
    width: '100%'
  },
  wrapper: {
    width: '57%',
    height: 230,
    marginTop: '14px'
  },
  slider: {
    paddingLeft: '10px'
  }
}

const SwipeableContent = ({children, dir}) => {
    return (
      <Typography component="div" dir={dir} >
        {children}
      </Typography>
    )
  }

SwipeableContent.propTypes = {
    children: PropTypes.node.isRequired,
    dir: PropTypes.string.isRequired,
}

class MidSlider extends Component {
    state = {
        value: 0
    }
    handleChangeIndex = index => {
      this.setState({ value: index })
    }
  render() {
    const { classes, theme } = this.props
    return (
        <SwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={this.state.value}
            onChangeIndex={this.handleChangeIndex}
            slideStyle={styles.wrapper}
            slideClassName={classes.slider} >
           
            <SwipeableContent dir={theme.direction}>
              <Card img={'http://pqpuavz67.bkt.clouddn.com/WX20190502-201125@2x.png'}
              title={''}
              info={''}
              />
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={'http://pqpuavz67.bkt.clouddn.com/mid2.png'}
              title={''}
              info={''}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={'http://pqpuavz67.bkt.clouddn.com/mid3.png'}
              title={''}
              info={''}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={'http://pqpuavz67.bkt.clouddn.com/mid7.png'}
              title={''}
              info={'Francisco Rossi3D'}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={'http://pqpuavz67.bkt.clouddn.com/mid4.png'}
              title={''}
              info={'Shan Jiang'}/>
            </SwipeableContent>
        </SwipeableViews>
    )
  }
}

MidSlider.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
  }
  
export default withStyles(styles, { withTheme: true })(MidSlider)
  
